@import url(https://fonts.googleapis.com/css2?family=Oswald:wght@200;300;400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

:root {
  --byte-grey: #929292;
  --byte-orange: #f4a41b;
  --byte-gold: #efc326;
  --shadow-green: rgb(32, 37, 32);
}

.hero-container {
  display: flex;
  height: 100%;
  flex-direction: column;
  justify-content: center;
}

.download {
  margin-top: 15px;
}
.download > h4 {
  margin-bottom: 0;
}

.download-line {
  width: 0%;
  height: 2px;
  background-color: rgb(149, 241, 74);
  transition: all 100ms ease;
}

.download:hover .download-line {
  width: 138px;
}

.social-links {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 180px;
  margin-top: 10px;
}

.social-links a {
  color: whitesmoke;
  text-decoration: none;
}

.footer {
  grid-area: footer;
  height: max(10vh, 100px);
  color: var(--byte-grey);
  background-color: rgb(29, 29, 29);
  box-sizing: border-box;
  display: flex;
  flex-flow: column wrap;
  justify-content: center;
}

body {
  overflow-x: hidden;
}

main {
  position: relative;
  display: flex;
  flex-flow: column;
  justify-content: center;
  align-items: flex-start;
  height: 100vh;
  /* background: linear-gradient(180deg, #efc326 0%, #ff5c00 100%); */
  background-color: #282c34;
}

section {
  width: 100%;
  height: 100%;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: cover;
  background-position: right center;
}

.fixed {
  position: fixed;
  top: 0;
  right: 17px;
  left: 0;
  bottom: 0;
}

.container {
  width: 90vw;
  max-width: 1100px;
  margin: 0 auto;
  height: 100%;
  padding: 0 20px;
  box-sizing: border-box;
}

.clicker {
  position: relative;
  background-color: yellowgreen;
  border: none;
  padding: 0.5rem 20px;
  margin: 1rem 0px;
  border-radius: 20px;
  box-shadow: 0px 4px rgb(57, 77, 18);
  min-width: 120px;
  outline: none;
  color: white;
  font-size: 1.2rem;
  font-weight: normal;
  font-family: "Oswald", "sans-serif";
}
.clicker:hover {
  cursor: pointer;
  background-color: rgb(125, 165, 45);
}
.clicker:active {
  transform: translateY(3px);
  box-shadow: 0px 2px rgb(57, 77, 18);
}

/* Default Styles */
h1,
h2,
h3,
h4,
h5,
h6,
p {
  font-family: "Oswald", "sans-serif";
  color: whitesmoke;
  margin: 0;
}
p {
  font-family: "Arial";
}
h1 {
  font-size: 4rem;
}
h2 {
  font-size: 2rem;
}
h3 {
  font-size: 1.5rem;
}
a {
  text-decoration: none;
}

@font-face {
  font-family: "A Goblin Appears";
  src: local("A Goblin Appears"), url("/static/media/A Goblin Appears.4c9c1f1e.otf");
}

